.about_us {
    height: 100vh;
    background: url('./../../imgs/fog.png') bottom/ contain no-repeat,
        url('./../../imgs/chef_aboutus.png') bottom (right 100px) no-repeat,
        url('./../../imgs/fog_rev.png') top/ contain no-repeat,
        url('./../../imgs/aboutUsBG.png') bottom/ cover no-repeat;
    min-height: fit-content;

    .chocolate {
        position: relative;
        float: right;
        z-index: 4;
    }

    .container {

        display: grid;
        grid-template-columns: 50% 50%;
    }

    &__content {
        position: relative;
        z-index: 2;

        &__text {
            &__title {
                font-family: Gilroy;
                font-size: 64.485px;
                line-height: 78px;

                h3 {
                    margin-bottom: 20px;
                    margin-top: 30px;
                }
            }

            &__description {
                p {
                    font-family: Gilroy;
                    font-weight: 500;
                    font-size: 20.0627px;
                    width: 767px;
                    line-height: 24px;
                }
            }

            // .wrap{
            //     display: grid;
            //     width: 800px;
            // }
            &__grid {
                display: grid;
                grid-template-columns: 315px 315px;
                // grid-template-columns: 1fr 1fr;
                grid-row-gap: 27px;
                // grid-column-gap: 38px;
                text-align: center;

                &__elem {
                    display: flex;
                    align-items: center;
                    min-height: 129px;
                    width: 290px;
                    background: #FFFFFF;
                    box-sizing: border-box;
                    border-radius: 5px;

                    .wrap {
                        display: flex;
                        align-items: center;
                        margin: 10px;
                        width: 100%;
                        height: 119px;
                        border: 0.2px dashed #000000;
                        border-radius: 5px;

                        p {
                            font-family: Gilroy;
                            font-style: normal;
                            width: 252px;
                            margin: 0 auto;
                            font-weight: 500;
                            font-size: 15px;
                            line-height: 18px;
                            text-align: center;
                        }
                    }

                }
            }

        }

        &__button {
            margin-top: 60px;

            button {
                font-family: Gilroy;
                font-weight: 500;
                font-size: 20px;
                width: 295px;
                height: 80px;
            }
        }
    }

    &__image {
        position: relative;
        top: 100px;
        z-index: 1;
        display: flex;
        justify-self: flex-end;
        align-items: flex-end;
        vertical-align: bottom;
    }
}

@media screen and(max-width:1150px) {

    .about_us {
        height: fit-content;
        background: url('./../../imgs/fog.png') bottom/ contain no-repeat,
            url('./../../imgs/chef_aboutus.png') bottom (right -70px) no-repeat,
            url('./../../imgs/fog_rev.png') top/ contain no-repeat,
            url('./../../imgs/aboutUsBG.png') bottom/ cover no-repeat;

        background-size: auto, 400px, auto, auto;
        min-height: fit-content;

        .chocolate {
            // position: relative;
            // float: right;
            // z-index: 4;
            display: none;
        }

        .container {
            display: flex;
            grid-template-columns: 1fr;
        }

        &__content {
            position: relative;
            z-index: 2;

            &__text {
                &__title {
                    font-family: Gilroy;
                    font-size: 64.485px;
                    line-height: 78px;

                    h3 {
                        margin-bottom: 20px;
                        margin-top: 30px;
                        text-align: center;
                    }
                }

                &__description {
                    p {
                        font-family: Gilroy;
                        font-weight: 500;
                        font-size: 20.0627px;
                        width: auto;
                        line-height: 24px;
                    }
                }

                &__grid {
                    display: grid;
                    grid-template-columns: 180px;
                    // grid-template-columns: 1fr 1fr;
                    grid-row-gap: 27px;
                    // grid-column-gap: 38px;
                    text-align: center;

                    &__elem {
                        display: flex;
                        align-items: center;
                        min-height: 110px;
                        width: 200px;
                        background: #FFFFFF;
                        box-sizing: border-box;
                        border-radius: 5px;

                        .wrap {
                            display: flex;
                            align-items: center;
                            margin: 10px;
                            width: 180px;
                            height: auto;
                            border: 0.2px dashed #000000;
                            border-radius: 5px;

                            p {
                                font-family: Gilroy;
                                font-style: normal;
                                width: auto;
                                margin: 0 auto;
                                font-weight: 500;
                                font-size: 15px;
                                line-height: 18px;
                                text-align: center;
                            }
                        }

                    }
                }

            }

            &__button {
                margin-top: 60px;
                display: flex;
                justify-content: center;
                position: relative;
                top: 100px;
                button {
                    font-family: Gilroy;
                    font-weight: 500;
                    font-size: 20px;
                    width: 295px;
                    height: 80px;
                }
            }
        }

        &__image {
            position: relative;
            top: 100px;
            z-index: 1;
            display: flex;
            justify-self: flex-end;
            align-items: flex-end;
            vertical-align: bottom;
        }
    }



}

/* Landscape phones and smaller */
@media (max-width: 480px) {
    .about_us__content__text__grid__elem .wrap p {
        padding: 10px
    }
}